export default {
  name: 'FiatWalletHistoryWithdraw',

  props: {
    historyData: {
      type: Array,
      required: true,
    },
    title: {
      type: String,
      required: true,
    },
  },

  data: () => ({
    columns: null,
    withdrawColumns: null,
    loading: false,
  }),

  beforeMount() {
    this.columns = [
      {
        title: 'Birim',
        dataIndex: 'Asset',
        width: '15%',
        scopedSlots: { customRender: 'asset' },
      },
      {
        title: 'Durum',
        dataIndex: 'Status',
        width: '10%',
        scopedSlots: { customRender: 'status' },
      },
      {
        title: 'Miktar',
        dataIndex: 'Amount',
        width: '10%',
      },
      {
        title: 'IBAN',
        dataIndex: 'To',
        width: '35%',
      },
      {
        title: 'Tarih',
        dataIndex: 'DateTime',
        width: '25%',
        scopedSlots: { customRender: 'time' },
      },
      {
        title: '',
        dataIndex: '',
        width: '5%',
      },
    ]
  },

  methods: {
    handleTableChange(pagination, filters, sorter) {
    },

    getTime(val) {
      return new Date(val).toUTCString()
    },
  },
}
